.button {
    width: 81px;
    height: 20px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    color: #FFFFFF;


    flex: none;
    order: 0;
    flex-grow: 0;

}

.disabled {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 5px 24px;
    gap: 10px;

    width: 133px;
    height: 30px;
    left: 574px;
    top: 1573px;

    background: #004D99;
    opacity: 0.5;
    border-radius: 3px;



    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;

    /* identical to box height, or 143% */

    color: #002E5C;


    /* Inside auto layout */
    flex: none;
    order: 0;
    flex-grow: 0;


}

.secondary {
    box-sizing: border-box;

    /* Auto layout */
